import React from 'react';
import { FaFacebook, FaInstagram, FaLinkedin, FaTwitter, FaYoutube } from 'react-icons/fa';
import {FooterContainer, FooterLink, FooterLinkItems, FooterLinksContainer, FooterLinksWrapper, FooterLinkTitle,
        FooterWrap, SocialIcons, SocialMedia, SocialLogo, SocialMediaWrap, SocialIconLink, WebsiteRights, Img } from './FooterElements';
import {animateScroll as scroll} from 'react-scroll';


        const Footer = () => {

    const toggleHome = () => {
        scroll.scrollToTop();
    };
    return (
        <FooterContainer>
            <FooterWrap>
                <FooterLinksContainer>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle> About </FooterLinkTitle>
                                <FooterLink to='/signin'>My Works</FooterLink>
                                <FooterLink to='/signin'>Careers</FooterLink>
                                <FooterLink to='/signin'>Contact</FooterLink>                            
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle> Portfolio </FooterLinkTitle>
                                <FooterLink to='/signin'>Discover </FooterLink>
                                <FooterLink to='/signin'>Education</FooterLink>
                                <FooterLink to='/signin'>Travels</FooterLink>                            
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                    <FooterLinksWrapper>
                        <FooterLinkItems>
                            <FooterLinkTitle> Achievements </FooterLinkTitle>
                                <FooterLink to='/signin'>Sports </FooterLink>
                                <FooterLink to='/signin'>Careers</FooterLink>
                                <FooterLink to='/signin'>Contact </FooterLink>                            
                        </FooterLinkItems>
                        <FooterLinkItems>
                            <FooterLinkTitle> Social Media </FooterLinkTitle>
                                <FooterLink to='/signin'>Facebook</FooterLink>
                                <FooterLink to='/signin'>Twitter</FooterLink>
                                <FooterLink to='/signin'>Linkedin</FooterLink>                            
                        </FooterLinkItems>
                    </FooterLinksWrapper>
                </FooterLinksContainer>
                <SocialMedia>
                    <SocialMediaWrap>
                        <SocialLogo onClick= {toggleHome}>
                        <Img src='/logo.png' />  Sunday Dare 
                        </SocialLogo>
                        <WebsiteRights>
                            Sunday Dare &copy;  {new Date().getFullYear()}
                             &nbsp; | All Rights Reserved. 
                        </WebsiteRights>
                        <SocialIcons>
                            <SocialIconLink href='//www.facebook.com/' target='_blank' arial-label='Facebook'>
                                <FaFacebook />
                            </SocialIconLink>
                            <SocialIconLink href='//www.Instagram.com/' target='_blank' arial-label='Instagram'>
                                <FaInstagram />
                            </SocialIconLink>
                            <SocialIconLink href='//www.youtube.com' target='_blank' arial-label='Youtube'>
                                <FaYoutube />
                            </SocialIconLink>
                            <SocialIconLink href='https://twitter.com/SundayDareSD?lang=en' target='_blank' arial-label='Twitter'>
                                <FaTwitter />
                            </SocialIconLink>
                            <SocialIconLink href='https://www.linkedin.com/in/sunday-dare-10100025/' target='_blank' arial-label='Linkedin'>
                                <FaLinkedin />
                            </SocialIconLink>
                        </SocialIcons>
                    </SocialMediaWrap>
                </SocialMedia>
            </FooterWrap>
        </FooterContainer>
    )
}

export default Footer
