import React, {useEffect} from 'react'
import {ServicesContainer, ServicesH1, ServicesH2, ServicesIcon, ServicesCard, ServicesP,
        ServicesWrapper} from './ServicesElements';
import Icon1 from '../../images/nyif.jpg';
import Icon2 from '../../images/15.jpg';
import Icon3 from '../../images/14.jpg';
import Aos from 'aos';
import 'aos/dist/aos.css';


const Services = () => {

    useEffect(() => {

        Aos.init({ duration: 2000});

    }, []);

    return (
        <ServicesContainer id='services'>
            <ServicesH1>Achievements </ServicesH1>
            <ServicesWrapper>
                <ServicesCard data-aos='flip-left'>
                    <ServicesIcon src={Icon1}  />
                        <ServicesH2>Nigerian Youth Investment Fund.</ServicesH2>
                        <ServicesP>The Nigerian Youth Investment Fund (N-YIF), which was approved by the Federal Executive Council (FEC) late July, can easily pass as one of the biggest gains of Dare in the office. </ServicesP>
                    
                </ServicesCard>
                <ServicesCard data-aos='flip-up'>
                    <ServicesIcon src={Icon2} />
                        <ServicesH2>Rehabilitation Lagos and Abuja National Stadia</ServicesH2>
                        <ServicesP> The Minister announced that he had secured commitments from business moguls for the renovation of the Abuja and Lagos National stadia respectively. </ServicesP>
                    
                </ServicesCard>
                
                <ServicesCard data-aos='flip-right'>
                    <ServicesIcon src={Icon3} />
                        <ServicesH2>Sports Industry Policy</ServicesH2>
                        <ServicesP>There is a new Sports Industry Policy(SIP) in place. The new SIP will address four key areas “3 Is and 1 P” – Infrastructure, Investment, Incentives, and Policy, that will catapult sports in Nigeria to the next level. </ServicesP>
                    
                </ServicesCard>               
            </ServicesWrapper>            
        </ServicesContainer>
    )
}

export default Services
