import React, {useEffect} from 'react';
import { Button } from '../ButtonElement';
import {InfoContainer, InfoWrapper, InfoRow, Column2, Column1, TextWrapper, 
    TopLine, Heading, Subtitle, BtnWrap, ImgWrap, Img } from './InfoElements';
import Aos from 'aos';
import 'aos/dist/aos.css';


const InfoSection = ({lightText, lightBg, id, img, imgStart, topLine, headline, darkText,
                     description, buttonLabel, alt, primary, dark, dark2}) => {

        useEffect(() => {
            Aos.init({
                duration: 2000
            });

        }, []);
    return (
        <>
            <InfoContainer lightBg={lightBg} id={id} >
                <InfoWrapper >
                    <InfoRow imgStart={imgStart}>
                        <Column1 data-aos='fade-up'>
                            <TextWrapper>
                                <TopLine>{topLine}</TopLine>
                                    <Heading lightText={lightText}>{headline}</Heading>
                                     <Subtitle darkText={darkText}> {description}</Subtitle>
                                <BtnWrap>
                                 <Button to='home'
                                 smooth={true}
                                 duration={500}
                                 spy={true}
                                 exact='true'
                                 offset={-80}
                                 primary={primary ? 1 : 0}
                                 dark={dark ? 1 : 0}
                                 > {buttonLabel}</Button>
                                </BtnWrap>
                            </TextWrapper>
                        </Column1>
                        <Column2 data-aos='fade-down'>
                            <ImgWrap>
                                <Img src={img} alt={alt}/>
                            </ImgWrap>
                        </Column2>
                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>
        </>
    );
};

export default InfoSection
