import React, {useEffect} from 'react'
import {Container, FormWrap, Icon, FormContent, Form, FormH1, FormLabel, FormInput, FormButton, Img} from './SigninElements'
import Aos from 'aos';
import 'aos/dist/aos.css';


const Signin = () => {
    useEffect(() => {
        Aos.init({
            duration: 2000
        });

    }, []);

    return (
        <>
            <Container >
                <FormWrap >
                    <Icon to='/'>
                    <Img src='/logo.png' />
                        Sunday Dare </Icon>
                    <FormContent data-aos='flip-down'>
                        <Form action='a'>
                            <FormH1>Send a Message</FormH1>
                            <FormLabel htmlFor='for'>Full Name</FormLabel>
                            <FormInput type='text' required />
                            <FormLabel htmlFor='for'>Email</FormLabel>
                            <FormInput type='email' required />
                            <FormLabel htmlFor='for'>Message</FormLabel>
                            <FormInput type='message' required />
                            <FormButton type='submit'>Send</FormButton>
                        </Form>
                    </FormContent>
                </FormWrap>
            </Container>
        </>
    )
}

export default Signin
